'use client';

import { useEffect, useState } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/_components/ui/card';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/_components/ui/table';
import { Skeleton } from '@/_components/ui/skeleton';

type Customer = {
  id: string;
  name: string;
  email: string;
  lastPurchase: string;
};

export function CustomerTable() {
  const [customers, setCustomers] = useState<Customer[] | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // Simulando chamada para o back-end (substituir por fetch/axios)
    setTimeout(() => {
      setCustomers([
        {
          id: '1',
          name: 'Joana Silva',
          email: 'joana@email.com',
          lastPurchase: '2025-04-01',
        },
        {
          id: '2',
          name: 'Carlos Alberto',
          email: 'carlos@email.com',
          lastPurchase: '2025-04-03',
        },
        {
          id: '3',
          name: 'Ana Paula',
          email: 'ana@email.com',
          lastPurchase: '2025-04-02',
        },
      ]);
      setLoading(false);
    }, 1500);
  }, []);

  return (
    <Card>
      <CardHeader>
        <CardTitle className="text-lg font-semibold">Clientes recentes</CardTitle>
      </CardHeader>
      <CardContent className="overflow-x-auto">
        <Table>
          <TableHeader>
            <TableRow>
              <TableHead className="min-w-[150px]">Nome</TableHead>
              <TableHead className="min-w-[200px]">Email</TableHead>
              <TableHead className="min-w-[150px]">Última compra</TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {loading ? (
              Array.from({ length: 3 }).map((_, i) => (
                <TableRow key={i}>
                  <TableCell><Skeleton className="h-4 w-32" /></TableCell>
                  <TableCell><Skeleton className="h-4 w-48" /></TableCell>
                  <TableCell><Skeleton className="h-4 w-24" /></TableCell>
                </TableRow>
              ))
            ) : (
              customers?.map((customer) => (
                <TableRow key={customer.id}>
                  <TableCell className="font-medium">{customer.name}</TableCell>
                  <TableCell>{customer.email}</TableCell>
                  <TableCell>{new Date(customer.lastPurchase).toLocaleDateString('pt-BR')}</TableCell>
                </TableRow>
              ))
            )}
          </TableBody>
        </Table>
      </CardContent>
    </Card>
  );
}
